import { h, render } from 'preact';
import { RtcServer } from './RtcServer';
import { RtcClient } from './RtcClient';
import { useState, useEffect } from 'preact/hooks';
import { getIceServers } from './turnCreds';
import { WebDavSignal } from './WebDavSignal';
import { RtcState } from './RtcBase';
import { ConfigManager } from './ConfigManager';

const configManager = new ConfigManager();

function SyncData({ value, onChange, disabled }) {
  let link = '';
  value = value.trim();
  if (value.match(/^https?:/)) {
    link = (
      <a href={value} target="_blank">
        Open link
      </a>
    );
  }
  return (
    <div>
      <div class="field">
        <div class="control">
          <textarea
            class="textarea is-medium"
            value={value}
            onInput={(e) => onChange(e.currentTarget.value)}
            disabled={disabled}
          ></textarea>
        </div>
      </div>
      {link}
    </div>
  );
}

function Server({ iceServers }) {
  if (!configManager.isValid) {
    return <ConfigError />;
  }
  const signal = new WebDavSignal(configManager.webDavCreds);
  const id = configManager.deviceId;
  const [state, setState] = useState(RtcState.INACTIVE);
  const [input, setInput] = useState('');
  const [server, setServer] = useState(null);
  useEffect(() => {
    const server = new RtcServer({ iceServers }, signal);
    server.addEventListener('message', ({ data }) => {
      setInput(data);
    });
    server.addEventListener('state', (event) => {
      setState(event.state);
    });
    server.start(id);
    setServer(server);
    return () => {
      server.close();
    };
  }, [id]);
  const onChange = (value) => {
    setInput(value);
    server.sendMessage(value);
  };
  return (
    <SyncData
      value={input}
      onChange={onChange}
      disabled={state !== RtcState.CONNECTED}
    />
  );
}

function Client({ id, iceServers }) {
  const signal = new WebDavSignal(configManager.webDavCreds);
  const [state, setState] = useState(RtcState.INACTIVE);
  const [input, setInput] = useState('');
  const [client, setClient] = useState(null);
  useEffect(() => {
    const client = new RtcClient({ iceServers }, signal);
    client.addEventListener('message', ({ data }) => {
      setInput(data);
    });
    client.addEventListener('state', (event) => {
      setState(event.state);
    });
    client.connect(id);
    setClient(client);
    return () => {
      client.close();
    };
  }, [id]);
  const onChange = (value) => {
    setInput(value);
    client.sendMessage(value);
  };
  return (
    <SyncData
      value={input}
      onChange={onChange}
      disabled={state !== RtcState.CONNECTED}
    />
  );
}

function ClientPicker({ iceServers }) {
  if (!configManager.isValid) {
    return <ConfigError />;
  }
  const signal = new WebDavSignal(configManager.webDavCreds);
  const [id, setId] = useState('');
  const [active, setActive] = useState([]);
  useEffect(() => {
    const updater = async () => {
      setActive(await signal.listActiveIds());
    };
    updater();
    const interval = setInterval(updater, 5000);
    return () => {
      clearInterval(interval);
    };
  }, []);
  const activeList = active.map((activeId) => (
    <button
      class={'button is-rounded' + (activeId === id ? ' is-primary' : '')}
      onClick={() => {
        setId(activeId);
      }}
    >
      {activeId}
    </button>
  ));
  let client = '';
  if (id) {
    client = <Client id={id} iceServers={iceServers} />;
  }
  return (
    <div>
      <div class="buttons">{activeList}</div>
      {client}
    </div>
  );
}

function ConfigError() {
  return (
    <article class="message is-warning">
      <div class="message-header">
        <p>Config error</p>
        <button class="delete" aria-label="delete"></button>
      </div>
      <div class="message-body">Missing or invalid config.</div>
    </article>
  );
}

function Config({ isOpen, onClose }) {
  const updateWebDavUrl = (e) => {
    configManager.webDavUrl = e.currentTarget.value;
  };
  const updateWebDavUsername = (e) => {
    configManager.webDavUsername = e.currentTarget.value;
  };
  const updateWebDavPassword = (e) => {
    configManager.webDavPassword = e.currentTarget.value;
  };
  const updateDeviceId = (e) => {
    configManager.deviceId = e.currentTarget.value;
  };
  const updateMeteredApiKey = (e) => {
    configManager.meteredApiKey = e.currentTarget.value;
  };
  return (
    <div>
      <div class="field">
        <label class="label">WebDav URL</label>
        <div class="control">
          <input
            class="input"
            type="url"
            placeholder="https://webdav.example/folder"
            value={configManager.webDavUrl}
            onInput={updateWebDavUrl}
          />
        </div>
      </div>

      <div class="field">
        <label class="label">WebDav username</label>
        <div class="control">
          <input
            class="input"
            type="text"
            value={configManager.webDavUsername}
            onInput={updateWebDavUsername}
          />
        </div>
      </div>

      <div class="field">
        <label class="label">WebDav password</label>
        <div class="control">
          <input
            class="input"
            type="password"
            value={configManager.webDavPassword}
            onInput={updateWebDavPassword}
          />
        </div>
      </div>

      <div class="field">
        <label class="label">Device name</label>
        <div class="control">
          <input
            class="input"
            type="text"
            value={configManager.deviceId}
            onInput={updateDeviceId}
          />
        </div>
      </div>

      <div class="field">
        <label class="label">metered.ca API key</label>
        <div class="control">
          <input
            class="input"
            type="text"
            value={configManager.meteredApiKey}
            onInput={updateMeteredApiKey}
          />
        </div>
      </div>
    </div>
  );
}

function Nav({ active, onTabClick }) {
  return (
    <div class="tabs is-centered is-boxed is-medium">
      <ul>
        <li
          class={active !== 'client' && active !== 'server' ? 'is-active' : ''}
        >
          <a onClick={() => onTabClick('config')}>
            <span class="icon is-small">
              <i class="fas fa-image" aria-hidden="true"></i>
            </span>
            <span>Config</span>
          </a>
        </li>
        <li class={active === 'server' ? 'is-active' : ''}>
          <a onClick={() => onTabClick('server')}>
            <span class="icon is-small">
              <i class="fas fa-music" aria-hidden="true"></i>
            </span>
            <span>Server</span>
          </a>
        </li>
        <li class={active === 'client' ? 'is-active' : ''}>
          <a onClick={() => onTabClick('client')}>
            <span class="icon is-small">
              <i class="fas fa-film" aria-hidden="true"></i>
            </span>
            <span>Client</span>
          </a>
        </li>
      </ul>
    </div>
  );
}

function App() {
  const [active, setActive] = useState(location.hash.replace('#', ''));
  const [iceServers, setIceServers] = useState();
  const onTabClick = (tab) => {
    history.pushState(null, null, '#' + tab);
    setActive(tab);
  };
  useEffect(async () => {
    setIceServers(await getIceServers(configManager.meteredApiKey));
  }, []);
  useEffect(() => {
    const listener = () => {
      setActive(location.hash.replace('#', ''));
    };
    addEventListener('hashchange', listener);
    addEventListener('popstate', listener);
    return () => {
      removeEventListener('hashchange', listener);
      removeEventListener('popstate', listener);
    };
  }, []);

  let content = <Config />;
  if (active === 'server') {
    content = <Server iceServers={iceServers} />;
  } else if (active === 'client') {
    content = <ClientPicker iceServers={iceServers} />;
  }
  return (
    <div class="container">
      <div class="box">
        <Nav active={active} onTabClick={onTabClick} />
        {content}
      </div>
    </div>
  );
}

render(<App />, document.body);
